import React, { useState, useEffect } from "react";
import {
  Box,
  Typography,
  Button,
  Card,
  Avatar,
  Stack,
  Chip,
} from "@mui/material";
import { deepPurple } from "@mui/material/colors";
import { deleteUser, getAllUsers } from "../../lib/api";

const AdminUserList = () => {
  const [users, setUsers] = useState([]);
  useEffect(() => {
    // API   
    const fetchAllUsers = async () => {
      try {
        const response = await getAllUsers();
        console.log(" ", response);
        setUsers(response.data.users); //  
      } catch (error) {
        if (error.response.status === 401) {
          alert("   .");
        }
      }
    };
    fetchAllUsers(); //  
  }, []);

  const handleDeleteUser = async (shortId) => {
    try {
      const response = await deleteUser(shortId);
      console.log("  response", response);
      if (response.status === 204) {
        const updatedUsers = users.filter((user) => user.shortId !== shortId);
        setUsers(updatedUsers);
      }
    } catch (error) {
      console.log("  ");
    }
  };

  return (
    <div style={{ display: "flex", minHeight: "100vh" }}>
      <Box sx={{ maxWidth: 600, mx: "auto" }}>
        <Typography variant="h6">  ({users.length})</Typography>
        <Stack spacing={2}>
          {users.map((user) => (
            <Card
              key={user.id}
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                mb: 2,
                p: 2,
              }}
            >
              <Box sx={{ display: "flex", alignItems: "center" }}>
                <Avatar sx={{ bgcolor: deepPurple[500], mr: 2 }}>
                  {user.userName}
                </Avatar>
                <Box>
                  <Typography variant="subtitle1">
                    {user.userName}{" "}
                    {user.role === "ADMIN" && (
                      <Chip label="" size="small" color="primary" />
                    )}
                  </Typography>
                  <Typography variant="body2" component="span">
                    {user.email}  : {user.phone}
                  </Typography>
                  <Typography variant="body2">
                    : {user.age}  : {user.address}
                  </Typography>
                </Box>
              </Box>
              <Button
                variant="contained"
                color="error"
                onClick={() => handleDeleteUser(user.shortId)}
              >
                
              </Button>
            </Card>
          ))}
        </Stack>
      </Box>
    </div>
  );
};

export default AdminUserList;
