import React, { useState, useEffect } from "react";
import {
  Box,
  Typography,
  Button,
  Card,
  Divider,
  Select,
  MenuItem,
} from "@mui/material";
import { deleteOrder, getAllOrders, orderStatusChange } from "../../lib/api";

const AdminOrderList = () => {
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    const fetchOrders = async () => {
      try {
        const response = await getAllOrders();
        setOrders(response.data.allOrders);
      } catch (error) {
        console.error("Error fetching orders:", error);
      }
    };

    fetchOrders();
  }, []);

  const handleOrderDelete = async (userId, orderNumber) => {
    try {
      const response = await deleteOrder(userId, orderNumber);
      if (response.status === 200) {
        const updatedOrders = orders.filter(
          (order) => order.orderNumber !== orderNumber
        );
        setOrders(updatedOrders);
      }
    } catch (error) {
      console.error("Order deletion failed", error);
      if (error.response.status === 400) {
        alert("    .");
      } else if (
        error.response.error ===
        "    :    ."
      ) {
        alert(
          "    :    ."
        );
      } else if (
        error.response.error ===
        "     :     ."
      ) {
        alert(
          "     :     ."
        );
      }
    }
  };

  const handleStatusChange = async (event, orderNumber) => {
    const newStatus = event.target.value;
    try {
      const response = await orderStatusChange(orderNumber, {
        status: newStatus,
      });
      if (response.status === 200) {
        const updatedOrders = orders.map((order) => {
          if (order.orderNumber === orderNumber) {
            return { ...order, status: newStatus };
          }
          return order;
        });

        setOrders(updatedOrders);
      }
    } catch (error) {
      console.log("   ");
    }
  };

  const renderOrderSection = (statusLabel) => {
    return orders
      .filter((order) => order.status === statusLabel)
      .map((order) => (
        <React.Fragment key={order._id}>
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              mb: 2,
            }}
          >
            <Typography variant="body1" fontWeight="bold">
               : {order.orderNumber}
            </Typography>
            <Select
              value={order.status}
              onChange={(event) => handleStatusChange(event, order.orderNumber)}
              size="small"
              sx={{ ml: 2 }}
            >
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
            </Select>
          </Box>

          <Card
            sx={{
              mb: 2,
              p: 2,
              border: "1px solid #ccc",
              borderRadius: "4px",
            }}
          >
            {order.products.map((item) => (
              <Box key={item.productInfo.carId} sx={{ mt: 1, mb: 1 }}>
                <Typography variant="body1">
                  {item.productInfo.carName}
                </Typography>
                <Typography variant="body2">
                  : {item.productInfo.color}
                </Typography>
                <Typography variant="body2">
                  : {item.productInfo.carPrice.toLocaleString()}
                </Typography>
                <Divider />
              </Box>
            ))}
          </Card>
          <Box sx={{ mt: 2, display: "flex", justifyContent: "space-between" }}>
            <Typography variant="body1" fontWeight="bold">
              (): ({order.address})
            </Typography>
            <Typography variant="body1" fontWeight="bold">
                : {order.totalAmount.toLocaleString()}
            </Typography>
            <Button
              variant="contained"
              color="primary"
              onClick={() => handleOrderDelete(order.userId, order.orderNumber)}
            >
               
            </Button>
          </Box>
        </React.Fragment>
      ));
  };

  return (
    <Box style={{ minHeight: "100vh" }}>
      <Typography variant="h6" sx={{ mb: 2 }} fontWeight="bold">
          ({orders.length})
      </Typography>

      <Typography variant="h6" sx={{ mt: 4, mb: 2 }} fontWeight="bold">
         
      </Typography>
      {renderOrderSection(" ")}

      <Typography variant="h6" sx={{ mt: 4, mb: 2 }} fontWeight="bold">
        
      </Typography>
      {renderOrderSection(" ")}

      <Typography variant="h6" sx={{ mt: 4, mb: 2 }} fontWeight="bold">
         
      </Typography>
      {renderOrderSection(" ")}

      <Typography variant="h6" sx={{ mt: 4, mb: 2 }} fontWeight="bold">
         
      </Typography>
      {renderOrderSection(" ")}
    </Box>
  );
};

export default AdminOrderList;
