import React, { useState } from "react";
import {
  Container,
  TextField,
  Button,
  Grid,
  Paper,
  Typography,
} from "@mui/material";
import { Link, useNavigate } from "react-router-dom";
import { signup } from "../../lib/api";

const CenteredLink = (props) => (
  <Link
    style={{
      display: "flex",
      justifyContent: "center",
      marginTop: "20px",
    }}
    {...props}
  />
);

const CenteredTitle = (props) => (
  <h2
    style={{
      textAlign: "center",
    }}
    {...props}
  />
);

const Register = () => {
  const navigate = useNavigate();

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [role, setRole] = useState("USER");
  const [emailError, setEmailError] = useState("");
  const [shortId, setShortId] = useState("");

  const validateEmail = (e) => {
    setEmail(e.target.value);
    const re = /\S+@\S+\.\S+/;
    if (!re.test(e.target.value)) {
      setEmailError("  .");
    } else {
      setEmailError("");
    }
  };

  const validatePassword = (e) => {
    setPassword(e.target.value);
  };

  const validateConfirmPassword = (e) => {
    setConfirmPassword(e.target.value);
  };

  const handleNameChange = (e) => {
    setName(e.target.value);
  };

  const handleRoleChange = (e) => {
    setRole(e.target.value);
  };

  const handleRegistration = async () => {
    if (emailError) {
      alert("   .");
      return; //       .
    }

    if (password !== confirmPassword) {
      alert("  .");
      return; //       .
    }

    // API   
    const userData = {
      userName: name,
      email: email,
      password: password,
      role: role,
    };

    try {
      // signup  await   .
      const response = await signup(userData);
      if (response.status === 201) {
        //  
        alert(" .");
        // shortId 
        setShortId(response.data.user.shortId); // API  shortId .

        //   shortId 
        localStorage.setItem("shortId", response.data.user.shortId);
        navigate("/login"); //   
      }
    } catch (error) {
      console.log(error);
      if (error.response.data.message === "  .") {
        alert("  .");
      } else if (
        error.response.data.message ===
        ", ,    ."
      ) {
        alert(", ,  .");
      }
    }
  };

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        height: "100vh",
      }}
    >
      <Container maxWidth="sm">
        <Paper elevation={3} style={{ padding: "20px", marginTop: "20px" }}>
          <CenteredTitle></CenteredTitle>
          <form>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Typography variant="h7" sx={{ fontWeight: "bold" }}>
                  
                </Typography>
                <TextField fullWidth value={name} onChange={handleNameChange} />
              </Grid>
              <Grid item xs={12}>
                <Typography variant="h7" sx={{ fontWeight: "bold" }}>
                  
                </Typography>
                <TextField
                  fullWidth
                  onChange={validateEmail}
                  error={!!emailError}
                  value={email}
                  helperText={emailError}
                />
              </Grid>
              <Grid item xs={12}>
                <Typography variant="h7" sx={{ fontWeight: "bold" }}>
                  
                </Typography>
                <TextField
                  fullWidth
                  type="password"
                  onChange={validatePassword}
                />
              </Grid>
              <Grid item xs={12}>
                <Typography variant="h7" sx={{ fontWeight: "bold" }}>
                   
                </Typography>
                <TextField
                  fullWidth
                  type="password"
                  value={confirmPassword}
                  onChange={validateConfirmPassword}
                />
              </Grid>
              <Grid item xs={12}>
                <Button
                  variant="contained"
                  color="primary"
                  fullWidth
                  onClick={handleRegistration}
                >
                  
                </Button>
              </Grid>
            </Grid>
          </form>
          <CenteredLink to="/login">
                !
          </CenteredLink>
        </Paper>
      </Container>
    </div>
  );
};

export default Register;
