import React, { useState, useEffect } from "react";
import { getAllProducts, deleteProduct } from "../../../lib/api";
import { Typography, Box, Button, Stack, Modal, Grid } from "@mui/material";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline"; // MUI  
import AdminItemAddModal from "./AdminItemAddModal";

const AdminItem = () => {
  const [products, setProducts] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);

  useEffect(() => {
    //      
    getAllProducts().then((response) => {
      setProducts(response.data.car);
    });
  }, []);

  //  
  const handleDeleteProduct = async (productId) => {
    console.log(productId);
    try {
      const response = await deleteProduct(productId);
      console.log(response);
      if (response.status === 204) {
        // Update products state after successful deletion
        const updatedProducts = products.filter(
          (product) => product.carId !== productId
        );
        setProducts(updatedProducts);
      }
    } catch (error) {
      console.error(error);
      if (error.response.status === 404) {
        alert("   .");
      }
    }
  };

  const handleAddProduct = () => {
    setModalOpen(true);
  };

  const handleCloseModal = () => {
    setModalOpen(false);
  };

  return (
    <div
      style={{ display: "flex", minHeight: "100vh", flexDirection: "column" }}
    >
      <Stack
        direction="row"
        justifyContent="flex-start"
        alignItems="flex-start"
        spacing={2} //   
        sx={{ flexWrap: "wrap" }} //   
      >
        <Typography variant="h5">   </Typography>
        <Button
          variant="contained"
          color="primary"
          startIcon={<AddCircleOutlineIcon />} //   
          onClick={handleAddProduct}
          sx={{ alignSelf: "flex-end" }} //    
        >
            
        </Button>
      </Stack>
      <Typography variant="subtitle1"> ({products.length})</Typography>
      <Modal
        open={modalOpen} // Modal   modalOpen  
        onClose={handleCloseModal} //       
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        <AdminItemAddModal
          open={modalOpen}
          handleClose={handleCloseModal}
          setProducts={setProducts}
        />
      </Modal>
      <Grid container spacing={2}>
        {products.map((product) => (
          <Grid item key={product.carId} xs={12} sm={6} md={4}>
            <Box
              key={product.carId}
              sx={{
                display: "flex",
                flexDirection: "row", //   
                alignItems: "flex-start", //    
                width: 300, //   
                backgroundColor: "#fff", //   
                boxShadow: "0 2px 8px rgba(0,0,0,0.1)", //   
                borderRadius: "8px", //    
                overflow: "hidden", //    
                mb: 2, //   
                position: "relative", //   
                height: "100%", //   100% 
              }}
            >
              <img
                src={product.img}
                alt={product.carName}
                style={{
                  width: "150px", //   
                  height: "100%",
                  objectFit: "cover", //   
                  borderRadius: "8px 0 0 8px", //      
                }}
              />
              <Box
                sx={{
                  padding: "0 16px", //  
                  textAlign: "left", //   
                  display: "flex", // flexbox  
                  flexDirection: "column", //    
                  justifyContent: "space-between", //    
                  width: "100%", //     
                }}
              >
                {/*   */}
                <Box>
                  <Typography
                    variant="subtitle1"
                    component="h2"
                    sx={{ fontWeight: "bold" }}
                  >
                    {product.carName}
                  </Typography>
                  <Typography variant="subtitle2">
                    : {product.speed} km/h
                  </Typography>
                  <Typography variant="subtitle2">
                    : {product.mileage} km
                  </Typography>
                  <Typography variant="subtitle2">
                    : {product.fuel}
                  </Typography>
                  <Typography variant="subtitle2">
                    : {product.color}
                  </Typography>
                  <Typography
                    variant="subtitle2"
                    sx={{ color: "red", mt: 1, fontWeight: "bold" }}
                  >
                     : {product.carPrice.toLocaleString()}
                  </Typography>
                </Box>
                <Button
                  onClick={() => handleDeleteProduct(product.carId)}
                  size="small"
                  variant="contained"
                  sx={{
                    backgroundColor: "red",
                    color: "white",
                    "&:hover": { backgroundColor: "darkred" },
                    padding: "6px 16px",
                    fontSize: "0.75rem",
                    alignSelf: "flex-start", //     
                    mt: 1, //   
                    mb: 1, //   
                  }}
                >
                   
                </Button>
              </Box>
            </Box>
          </Grid>
        ))}
      </Grid>
    </div>
  );
};

export default AdminItem;
